import React, { Component } from 'react';

class Todos extends Component{
  render(){
    return (
      <div>
        <h1>Todos</h1>
        <ul>  
        {this.props.todos.map((item) => (
          <li key={item.id}>{item.title}</li>
          ))}
        </ul>
      </div>
    );
  }
  
}

export default Todos;
